    html: '<div class="price-tag"></div>',
    css: '.price-tag {\n  display: inline-block;\n  \n  width: auto;\n  height: 38px;\n  \n  background-color: #333;\n  -webkit-border-radius: 3px 4px 4px 3px;\n  -moz-border-radius: 3px 4px 4px 3px;\n  border-radius: 3px 4px 4px 3px;\n  \n  border-left: 1px solid #333;\n\n  /* This makes room for the triangle */\n  margin-left: 19px;\n  \n  position: relative;\n  \n  color: white;\n  line-height: 38px;\n\n  padding: 0 10px 0 10px;\n}\n\n/* Makes the triangle */\n.price-tag:before {\n  content: "";\n  position: absolute;\n  display: block;\n  left: -19px;\n  width: 0;\n  height: 0;\n  border-top: 19px solid transparent;\n  border-bottom: 19px solid transparent;\n  border-right: 19px solid #333;\n}\n\n/* Makes the circle */\n.price-tag:after {\n  content: "";\n  background-color: white;\n  border-radius: 50%;\n  width: 4px;\n  height: 4px;\n  display: block;\n  position: absolute;\n  left: -9px;\n  top: 17px;\n}',
